import { AbstractEnv } from './env.abstract';

/**
 * 默认的Env环境，来源于frm
 */
export class DefaultEnv extends AbstractEnv {
    public getVersion(): string {
        return '';
    }
    public getFineServletURL(): string {
        return Report.fineServletURL;
    }
    public getReqPrefix(): string {
        return `${this.getFineServletURL()}/view/fit/form`;
    }
    public getSessionID(): string {
        // TODO SessionID需要从frm中获取
        return Report.SessionMgr.get();
    }
    public getInitParameters(): {[key: string]: any} | undefined {
        return undefined;
    }
    public getServletURL(): string {
        return `${this.getFineServletURL()}/view/form`;
    }
}
